export default {
	data() {
		return {
			imgList: [],
			info: {
				name: '',
				place:'',
				shortIntro: '',
				command: ''
			}
		}
	},
	onLoad() {

	},
	methods: {
		uploadInfo() {
			const db = wx.cloud.database({env:''});  // env-id
			db.collection('info').add({
				data: this.userinfo,
				success: function(res) {
					console.log(res)
				},
				fail: console.error()
			})
		},
		
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: ' ',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		}
	}
}
